.new {
  width: 100%;
  display: flex;

  .newContainer {
    flex: 6;

    .top,
    .bottom {
      -webkit-box-shadow: 1px 4px 6px -2px rgba(0, 0, 0, 0.33);
      -moz-box-shadow: 1px 4px 6px -2px rgba(0, 0, 0, 0.33);
      box-shadow: 1px 4px 6px -2px rgba(0, 0, 0, 0.33);
      padding: 10px;
      margin: 20px;
      display: flex;

      h1 {
        color: lightgray;
        font-size: 20px;

      }

      .left {
        flex: 1;
        text-align: center;

        img {
          width: 100px;
          height: 100px;
          border-radius: 50%;
          object-fit: cover;

        }
      }

      .right {
        flex: 2;

        form {
          display: flex;
          flex-wrap: wrap;
          gap: 30px;
          justify-content: space-around;

          .formInput {
            width: 40%;
          }

          label {
            display: flex;
            align-items: center;
            gap: 10px;

            .icon {
              cursor: pointer;

            }
          }

          input {
            width: 100%;
            padding: 5px;
            border: none;
            border-bottom: 1px solid gray;
            outline: none;
          }
        }

        button {
          width: 150px;
          padding: 10px;
          border: none;
          background-color: teal;
          color: white;
          font-weight: bold;
          cursor: pointer;
          margin: 10px;

          &:hover {
            background-color: rgba(0, 128, 128, 0.782);
          }
        }
      }
    }

  }
}