.featured {
  flex: 2;
  -webkit-box-shadow: 1px 4px 6px -2px rgba(0, 0, 0, 0.33);
  -moz-box-shadow: 1px 4px 6px -2px rgba(0, 0, 0, 0.33);
  box-shadow: 1px 4px 6px -2px rgba(0, 0, 0, 0.33);
  padding: 10px;
  border-radius: 2px;

  .top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: gray;

    .title {
      font-size: 16px;
      font-weight: 600;
    }
  }

  .bottom {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 15px;

    .featuredChart {
      width: 100px;
      height: 100px;
    }

    .title {
      font-weight: 500;
      color: gray;
    }

    .amount {
      font-size: 30px;

    }

    .desc {
      font-weight: 300;
      font-size: 12px;
      color: gray;
      text-align: center;
    }

    .summary {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .item {
        text-align: center;

        .itemTitle {
          font-size: 14px;
          color: gray;

        }
      }

      .itemResult {
        display: flex;
        align-items: center;
        margin-top: 10px;
        font-size: 13px;

        &.positive {
          color: green;
        }

        &.negative {
          color: red;
        }
      }
    }
  }
}